*{
  border: 0;
  padding: 0;
}
.headerWrapper{
  background-color: #1b1b1b;
  display: flex;
align-items: center;
  justify-content: center;
  flex-direction: column;
  .headerContainer{
padding: 5px;
    width: 69%;
    display: flex;
    justify-content: space-between;

    align-items: center;

    div{
      margin-left: 20px;
height: 26px;
      display: flex;
align-items: center;
      vertical-align: center;
      background-color: #252525;
      border-radius: 3px;
      padding: 0px 0px 0px 10px;
      img{
       margin: 0px 10px 0px 2px;
      }
    }
    .but{
margin-right: 130px;
      display: flex;
font-size: 12px;
      color: white;
justify-content: center;
      background-color: #2f2f2f;
      width: 98px;
      height: 26px;
padding: 0;
    }
span{

  width: 250px;
  text-align: right;
  font-size: 12px;
  color: #d2d2d2;
}

    input{
      width: 593px;
color: white;
      padding: 2px 5px 2px 0;
      background-color: #252525;

    }
  }
  .headerBottom{

    border-top: 1px solid #252525;
    height: 40px;
display: flex;
    justify-content: space-between;

    span{
      font-weight: 700;
font-size: 12px;
      border-bottom: 1px solid #1b1b1b;
      height: 38px;
      background-color: #1b1b1b;
      width: 165px;
      display: flex;
      flex-direction: column;

      justify-content: center;
      color: white;
    }
    span:hover{
      border-right:1px solid #252525;
      border-left: 1px solid #252525;
      border-bottom: 1px solid #FF9000;
      background-color: #171717;
      width: 163px;
    }


  }
}
