
.home{
  margin: 0;
  text-align: left;
  display: flex;
  flex-direction: column;
  align-items: center;
center{
  margin-top: 100px;
}
  .cards{
    display: flex;
justify-content: flex-start;
    flex-wrap: wrap;
    width: 1330px;

  }




}
@media screen and (max-width: 1580px){

  .home{

    margin: 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    center{
      margin-top: 100px;
    }
    .cards{

      display: flex;

      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      width: 1000px;

    }




  }
}